
.imgContent {
    width: 1024px;
    height: 300px;
    border-radius: 20px;
}

.descriptionBlock {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 10px;
    color: white;
    text-align: right;
}

.fullName {
    margin-bottom: 10px;
    font-size: 25px;
    font-family: monospace;
    font-weight: bold;
    color: grey;
}

.lookingForAJob {
    height: 50px;
    width: 70px;
}

.lookingForJobContainer {
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: red;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.lookingForAJobDescriptionContainer {
    text-decoration: underline;
}

.lookingForAJobDescription {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: brown;
}

.userAvatar {
    width: 200px;
    height: 200px;
    border-radius: 50%;
}

.status {
    font-family: 'Courier New', Courier, monospace;
}

.status:hover {
    cursor: pointer;
    border-bottom: 2px solid white;
}

.statusInEditMode {
    width: 300px;
    height: 25px;
}